import React from "react";
import { NavLink } from "react-router-dom";
import image from "../src/image/logo.jpg"

const Common = (props) => {

    return (
        <>
            <section id="header" className="d-flex align-items-center">
                <div className="container-fluid ">
                    <div className="row">
                        <div className="col-10 max-auto">

                            <div className="row">
                                <div className="col-md-6 mt-5 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column" >
                                    <h2>{props.name} <strong className="navbar-brand"> shubh technical</strong></h2>
                                    <p className="my-3">we are the team of taleted devloper  making website</p>


                                    <div className="mt-3">
                                        <NavLink to={props.visit} className="btn-get-started"><button type="button" class="btn btn-outline-success">{props.btname}</button></NavLink>

                                    </div>
                                </div>

                                <div id="imag" className="col-lg-6 mt-lg-5 mt-10 order-1 order-lg-2 header-img " alt="no load">
                                    <img src={props.imgsrc} className="img-fluid" />


                                </div>

                            </div>


                        </div>
                    </div>
                </div>
            </section>
            

        </>
    )
}
export default Common;