import React from "react";
import image from "../src/image/logo.jpg"
import Card from "./Card"
import Sdata from "./Sdata"


const Service = () => {
    return (
        <>
            <div className="my-3">
                <h1 className="text-center">Our services</h1>
            </div>


            <div id="serviceimg"className=" container-fluid mb-5 ">
                <div className="row">
                    <div className="col-10 max-auto">

                        <div className="row gy-4">
                       
                       {
                           Sdata.map((val,index)=>{
                               return <Card 
                               key={index}
                               imgsrc={val.imgsrc}
                               title={val.title}
                               />
                           })
                       }

                        






                        </div>
                    </div>

                </div>

            </div>
        </>
    )
}
export default Service;